import { predicateBasedOnProbability, randomFromRange } from "../utils/math";

export default class Circle {
  static MOVEMENT_OUTER_RADIUS = 5.5 as const;
  static MOVEMENT_INNER_RADIUS = 1.0 as const;
  static MOVEMENT_RHO = 1.5 as const;

  movementIndex: number = randomFromRange(0, Math.PI * 2);
  movementSlowdownFactor: number = randomFromRange(2500, 7080);
  shouldMoveClockwise: boolean = predicateBasedOnProbability(0.5);
  x: number;
  y: number;
  originalX: number;
  originalY: number;
  radius: number;
  ctx: CanvasRenderingContext2D;

  constructor(
    x: number,
    y: number,
    radius: number,
    ctx: CanvasRenderingContext2D
  ) {
    this.x = x;
    this.y = y;
    this.originalX = x;
    this.originalY = y;
    this.radius = radius;
    this.ctx = ctx;
  }

  setBaseX(x: number): this {
    this.x = x;
    this.originalX = x;

    return this;
  }

  setBaseY(y: number): this {
    this.y = y;
    this.originalY = y;

    return this;
  }

  update(elapsed: number) {
    const t = Math.PI * this.movementIndex;

    if (this.shouldMoveClockwise)
      this.movementIndex += elapsed / this.movementSlowdownFactor;
    else this.movementIndex -= elapsed / this.movementSlowdownFactor;
    this.x =
      this.originalX +
      ((Circle.MOVEMENT_OUTER_RADIUS - Circle.MOVEMENT_INNER_RADIUS) *
        Math.cos(t) +
        Circle.MOVEMENT_RHO *
          Math.cos(
            ((Circle.MOVEMENT_OUTER_RADIUS - Circle.MOVEMENT_INNER_RADIUS) /
              Circle.MOVEMENT_INNER_RADIUS) *
              t
          ));

    this.y =
      this.originalY +
      ((Circle.MOVEMENT_OUTER_RADIUS - Circle.MOVEMENT_INNER_RADIUS) *
        Math.sin(t) -
        Circle.MOVEMENT_RHO *
          Math.sin(
            ((Circle.MOVEMENT_OUTER_RADIUS - Circle.MOVEMENT_INNER_RADIUS) /
              Circle.MOVEMENT_INNER_RADIUS) *
              t
          ));

    return this;
  }

  draw() {
    this.ctx.save();
    this.ctx.translate(this.x, this.y);

    this.ctx.beginPath();
    this.ctx.arc(0, 0, this.radius, 0, Math.PI * 2);
    this.ctx.fillStyle = "white";
    this.ctx.fill();

    this.ctx.restore();

    return this;
  }
}
