export function distanceBetweenTwoPoints(
  x1: number,
  y1: number,
  x2: number,
  y2: number
): number {
  return Math.sqrt(Math.pow(x2 - x1, 2) + Math.pow(y2 - y1, 2));
}

export function randomFromRange(min: number, max: number): number {
  return Math.floor(Math.random() * (max - min) + min);
}

/**
 * @param probability Expects a number between 0 and 1
 */
export function predicateBasedOnProbability(probability: number): boolean {
  return Math.random() < probability;
}

// Hard to explain each parameter, better to simply see it visually: https://www.desmos.com/calculator/zb6perxq2g
export function gradientPointValue(point: number): number {
  const A = 5 as const;
  const B = 100 as const;
  const C = -0.005 as const;

  const value = A * Math.pow(Math.log(B), C * point);

  // truncates
  if (value > 1) return 1;
  if (value < 0.05) return 0;
  return value;
}
