type ConcreteFunctionType<A extends any[], R> = (...args: A) => R;

export default class Debounce<A extends any[], R> {
  private timeout?: NodeJS.Timeout | null;
  private handlerPromiseResponders?: {
    resolve: (value: void | R | PromiseLike<void | R>) => void;
    reject: (reason: string) => void;
  } | null;
  private cb: ConcreteFunctionType<A, R>;
  private delayMs: number;

  private constructor(cb: ConcreteFunctionType<A, R>, delayMs: number) {
    this.cb = cb;
    this.delayMs = delayMs;
  }

  static new<A extends any[], R>(
    cb: ConcreteFunctionType<A, R>,
    delayMs: number
  ) {
    const state = new Debounce(cb, delayMs);
    state.apply = state.apply.bind(state);
    state.abort = state.abort.bind(state);

    function innerApply(...args: A) {
      return state.apply(args);
    }
    innerApply.abort = state.abort;

    return innerApply;
  }

  apply(args: A) {
    return new Promise<R | void>((resolve, reject) => {
      this.handlerPromiseResponders = { resolve, reject };
      this.timeout = setTimeout(() => {
        try {
          resolve(this.cb(...args));
        } catch (e) {
          reject(e);
        } finally {
          clearTimeout(this.timeout as NodeJS.Timeout);
          this.timeout = null;
          this.handlerPromiseResponders = null;
        }
      }, this.delayMs);
    });
  }

  abort() {
    if (this.timeout && this.handlerPromiseResponders) {
      this.handlerPromiseResponders.resolve();
      clearTimeout(this.timeout);
      this.timeout = null;
      this.handlerPromiseResponders = null;
    }
  }
}
