import Circle from "./Circle";
import Line from "./Line";
import { predicateBasedOnProbability, randomFromRange } from "../utils/math";
import { State } from "../Canvas";

export interface QuadrantDimentions {
  w: number;
  h: number;
}

export default class Constellation {
  // TODO: Make rows and colums dynamic based on screen dimensions
  static readonly ROWS = 5 as const;
  static readonly COLUMNS = 7 as const;
  // ---
  static readonly PADDING = 20 as const;
  static readonly RADIUS_MIN = 2 as const;
  static readonly RADIUS_MAX = 7 as const;
  static readonly MAX_CIRCLES = Constellation.ROWS * Constellation.COLUMNS;
  static readonly LINES_AMOUNT =
    Constellation.COLUMNS -
    1 +
    (2 * (Constellation.ROWS - 1) +
      3 * (Constellation.COLUMNS - 1) * (Constellation.ROWS - 1));

  private circles: Circle[];
  private lines: Line[];
  private cnv: HTMLCanvasElement;

  get getCircles() {
    return Object.freeze(this.circles);
  }

  get getLines() {
    return Object.freeze(this.lines);
  }

  constructor(cnv: HTMLCanvasElement, ctx: CanvasRenderingContext2D) {
    const circles: Circle[] = Array(Constellation.MAX_CIRCLES);
    const lines: Line[] = Array(Constellation.LINES_AMOUNT);

    for (
      let i = 0;
      i <
      Constellation.MAX_CIRCLES +
        (Constellation.LINES_AMOUNT - Constellation.MAX_CIRCLES);
      i++
    ) {
      if (i < Constellation.MAX_CIRCLES) circles[i] = new Circle(0, 0, 0, ctx);
      lines[i] = new Line(ctx);
    }

    this.circles = circles;
    this.lines = lines;
    this.cnv = cnv;

    this.calculateParticleFieldPosition();
  }

  update(elapsed: number, state: State): this {
    for (
      let i = 0;
      i <
      Constellation.MAX_CIRCLES +
        (Constellation.LINES_AMOUNT - Constellation.MAX_CIRCLES);
      i++
    ) {
      if (i < Constellation.MAX_CIRCLES) this.circles[i].update(elapsed);
      this.lines[i].update(state);
    }

    return this;
  }

  render() {
    for (
      let i = 0;
      i <
      Constellation.MAX_CIRCLES +
        (Constellation.LINES_AMOUNT - Constellation.MAX_CIRCLES);
      i++
    ) {
      if (i < Constellation.MAX_CIRCLES) this.circles[i].draw();
      this.lines[i].draw();
    }
  }

  calculateParticleFieldPosition(): this {
    const { circles, cnv } = this;

    const quadrantDimentions: QuadrantDimentions = {
      w: (cnv.width - Constellation.PADDING) / Constellation.COLUMNS,
      h: (cnv.height - Constellation.PADDING) / Constellation.ROWS,
    };

    let linesCount = 0;
    for (let [y, i] = [0, 0]; y < Constellation.ROWS; y++) {
      for (let x = 0; x < Constellation.COLUMNS; ++x && ++i) {
        const x1 = quadrantDimentions.w * x + Constellation.PADDING;
        const y1 = quadrantDimentions.h * y + Constellation.PADDING;
        const x2 = quadrantDimentions.w * (x + 1) - Constellation.PADDING;
        const y2 = quadrantDimentions.h * (y + 1) - Constellation.PADDING;

        circles[i].setBaseX(randomFromRange(x1, x2));
        circles[i].setBaseY(randomFromRange(y1, y2));
        circles[i].radius = randomFromRange(
          Constellation.RADIUS_MIN,
          Constellation.RADIUS_MAX
        );
        const circle = circles[i];

        linesCount = this.connectCirclesBasedOnPositioning(circle, {
          x,
          y,
          i,
          currentLineIdx: linesCount,
        });
      }
    }

    return this;
  }

  private connectCirclesBasedOnPositioning(
    currentCircle: Circle,
    {
      x,
      y,
      i,
      currentLineIdx,
    }: {
      x: number;
      y: number;
      i: number;
      currentLineIdx: number;
    }
  ): number {
    const { lines, circles } = this;

    if (x === 0 && y === 0) return currentLineIdx;
    if (x > 0 && x < Constellation.COLUMNS) {
      const lastCircle = circles[i - 1];
      lines[currentLineIdx].circle1 = lastCircle;
      lines[currentLineIdx].circle2 = currentCircle;
      currentLineIdx++;
    }

    if (y === 0) return currentLineIdx;
    const upperCircle = circles[i - Constellation.COLUMNS];
    lines[currentLineIdx].circle1 = upperCircle;
    lines[currentLineIdx].circle2 = currentCircle;
    currentLineIdx++;

    if (x < Constellation.COLUMNS - 1) {
      if (predicateBasedOnProbability(0.5)) return currentLineIdx;
      const upperRightCircle = circles[i - Constellation.COLUMNS + 1];
      lines[currentLineIdx].circle1 = upperRightCircle;
      lines[currentLineIdx].circle2 = currentCircle;
      currentLineIdx++;
    }

    if (x === 0) return currentLineIdx;
    if (predicateBasedOnProbability(0.85)) return currentLineIdx;
    const upperLeftCircle = circles[i - (Constellation.COLUMNS + 1)];
    lines[currentLineIdx].circle1 = upperLeftCircle;
    lines[currentLineIdx].circle2 = currentCircle;
    currentLineIdx++;

    return currentLineIdx;
  }
}
