import Circle from "./Circle";
import { distanceBetweenTwoPoints, gradientPointValue } from "../utils/math";
import { State } from "../Canvas";

export default class Line {
  circle1?: Circle;
  circle2?: Circle;
  alpha?: number;
  ctx: CanvasRenderingContext2D;

  constructor(
    ctx: CanvasRenderingContext2D,
    circle1?: Circle,
    circle2?: Circle
  ) {
    this.circle1 = circle1;
    this.circle2 = circle2;
    this.ctx = ctx;
  }

  private setAlphaBasedOnMousePosition(state: State) {
    if (!this.circle1 || !this.circle2) {
      throw new Error(`Some circles are null or undefined: 
      {
        circle1: ${this.circle1},
        circle2: ${this.circle2}
      } 
      `);
    }

    const distanceFromCircles =
      distanceBetweenTwoPoints(this.circle1.x, this.circle1.y, state.pos.x, state.pos.y);
    distanceBetweenTwoPoints(this.circle2.x, this.circle2.y, state.pos.x, state.pos.y);

    let alpha = 1;

    if (state.alpha && !state.hold) {
      alpha = state.alpha;
      if (state.alpha > 0) {
        state.alpha -= 0.001
      }
    }

    this.alpha = gradientPointValue(distanceFromCircles) * alpha;
  }

  draw() {
    if (!this.circle1 || !this.circle2 || !this.alpha) {
      // simply ignore
      return this;
    }

    this.ctx.beginPath();
    this.ctx.moveTo(this.circle1.x, this.circle1.y);
    this.ctx.lineTo(this.circle2.x, this.circle2.y);

    this.ctx.strokeStyle = `rgba(255,255,255, ${this.alpha})`;
    this.ctx.stroke();

    this.ctx.closePath();

    return this;
  }

  update(state: State) {
    if (!this.circle1 || !this.circle2 || (state.pos.x < 0 && state.pos.y < 0)) {
      // simply ignore
      return this;
    }

    this.setAlphaBasedOnMousePosition(state);
    return this;
  }
}
