import { h } from "preact";
import { useEffect, useRef } from "preact/hooks";

import Canvas from "./canvas/Canvas";
import classes from "./CanvasBackground.module.css";

export default function CanvasBackground() {
  if (typeof window === "undefined") {
    return <div className={classes["fallbackBackground"]} />;
  }

  const canvas = useRef<Canvas | null>(null);

  useEffect(() => {
    const cnv = document.getElementById("canvas") as HTMLCanvasElement | null;
    const ctx = cnv?.getContext("2d");

    canvas.current = new Canvas(cnv, ctx);
    canvas.current.initCanvas();

    return canvas.current.destroy;
  }, []);

  return (
    <div className={classes["fallbackBackground"]}>
      <div className={classes["backdrop"]} />
      <canvas
        id="canvas"
        className={classes["fade"]}
        width={window.innerWidth}
        height={window.innerHeight}
      />
    </div>
  );
}
