import Constellation from "./entities/Constellation";
import Debounce from "./utils/debounce";

type Position = {
  x: number;
  y: number;
}

export type State = {
  pos: Position;
  alpha?: number;
  hold?: boolean;
};

export default class Canvas {
  static FRAME_DURATION = 1000 / 60 /* fps */;

  private cnv: HTMLCanvasElement;
  private ctx: CanvasRenderingContext2D;

  private constellation?: Constellation | null;

  private events:
    | {
      [key in keyof WindowEventMap]?: (ev: WindowEventMap[key]) => any;
    }
    | null = null;

  private state: State = { pos: { x: -1, y: -1 } };
  private currentFrameHandler = { frame: 0 };
  private last?: DOMHighResTimeStamp | null;
  private currTick: number = 0;

  constructor(
    cnv?: HTMLCanvasElement | null,
    ctx?: CanvasRenderingContext2D | null
  ) {
    if (!cnv || !ctx)
      throw new Error("An error occurred while initializing canvas");

    this.cnv = cnv;
    this.ctx = ctx;
  }

  initCanvas() {
    this.registerEvents();
    this.init();
  }

  destroy() {
    if (this.events) {
      for (const [type, callback] of Object.entries(this.events)) {
        window.removeEventListener(
          type as keyof WindowEventMap,
          callback as any
        );
      }
      this.events = null;
    }

    this.currTick = 0;
    this.currentFrameHandler.frame = 0;
    this.events = null;
    this.last = null;
  }

  private drawBackground() {
    const backgroundGradient = this.ctx.createLinearGradient(
      0,
      0,
      0,
      this.cnv.height
    );
    backgroundGradient.addColorStop(0, "#131a21");
    backgroundGradient.addColorStop(1, "#1a2d2d");

    this.ctx.fillStyle = backgroundGradient;
    this.ctx.fillRect(0, 0, this.cnv.width, this.cnv.height);
  }

  private registerEvents() {
    // TODO: Could be done more cleanly?

    const doTouch = function(e: TouchEvent) {
      //@ts-ignore
      const _this = this as Canvas;
      _this.state.pos.x = e.changedTouches[0].clientX;
      _this.state.pos.y = e.changedTouches[0].clientY;
      _this.state.alpha = 1;
      _this.state.hold = e.type === "touchmove";
    }.bind(this)

    const events: typeof this.events = {
      mousemove: function(e: MouseEvent) {
        // Hack :C Typescript does not seem to support 'this' binding yet
        //@ts-ignore
        const _this = this as Canvas;
        _this.state.pos.x = e.clientX;
        _this.state.pos.y = e.clientY;
      }.bind(this),
      touchstart: doTouch,
      touchmove: doTouch,
      touchend: doTouch,
      resize: this.init.bind(this),
    };

    for (const [type, callback] of Object.entries(events)) {
      window.addEventListener(type as keyof WindowEventMap, callback as any);
    }

    this.events = events;
  }

  private _setupAndInit() {
    this.cnv.width = window.innerWidth;
    this.cnv.height = window.innerHeight;
    this.initAnimation();
    this.cnv.style.opacity = "1";
  }

  private debounceInit = Debounce.new(this._setupAndInit.bind(this), 1500);

  private async init() {
    cancelAnimationFrame(this.currentFrameHandler.frame);

    this.debounceInit.abort();
    this.cnv.style.opacity = "0";
    await this.debounceInit();
  }

  private initAnimation() {
    if (this.constellation) this.constellation.calculateParticleFieldPosition();
    else this.constellation = new Constellation(this.cnv, this.ctx);

    this.currTick = 0;

    this.currentFrameHandler.frame = window.requestAnimationFrame(
      this.tick.bind(this)
    );
  }

  private tick(now: DOMHighResTimeStamp) {
    if (!this.last) this.last = now;

    const elapsed = now - this.last;
    if (elapsed > Canvas.FRAME_DURATION) {
      this.update(elapsed);
      this.render();

      this.last = now;
      this.currTick++;
    }
    this.currentFrameHandler.frame = window.requestAnimationFrame(
      this.tick.bind(this)
    );
  }

  private update(elapsed: number) {
    if (!this.constellation) {
      throw new Error(
        `Update function called while 'constellation' is ${this.constellation}`
      );
    }
    this.constellation.update(elapsed, this.state);
  }

  private render() {
    if (!this.constellation) {
      throw new Error(
        `Render function called while 'constellation' is ${this.constellation}`
      );
    }

    this.drawBackground();
    this.constellation.render();
  }
}
