import { h, Fragment } from "preact";
import { Link } from "preact-router";

import { jobs, jobsSortedByStartDate } from "src/data/jobs";
import { RouteAwareProps } from "src/declarations";
import Footer from "src/components/Footer";
import jobArticleClasses from "./JobArticle.module.css";
import NotFound from "src/routes/not-found";
import Section from "src/components/Section";

type UrlParameter = {
  id?: string;
};

type MappedLinkedList = Record<
  string,
  {
    prev: string | undefined;
    next: string | undefined;
  }
>;

const jobsLinkedList = jobsSortedByStartDate.reduce<MappedLinkedList>(
  (acc, [id], idx) => {
    acc[id] = {
      // Latest jobs first
      prev: jobsSortedByStartDate[idx + 1]?.[0] as string | undefined,
      next: jobsSortedByStartDate[idx - 1]?.[0] as string | undefined,
    };
    return acc;
  },
  {}
);

function scrollToTop() {
  window.scrollTo(0, 0);
}

export default function JobArticle({
  id,
}: Readonly<RouteAwareProps & UrlParameter>) {
  if (!id || !jobs[id]) {
    return <NotFound />;
  }

  const job = jobs[id];
  const previousJobId: string | undefined = jobsLinkedList[id].prev;
  const nextJobId: string | undefined = jobsLinkedList[id].next;

  return (
    <>
      <Section className={jobArticleClasses["job-article-root"]}>
        <h1>Expertise</h1>
        <hr />
        <Link className={jobArticleClasses["go-back-link"]} href="/expertise">
          {"\u2190 Go back"}
        </Link>
        <h2>{job.name}</h2>
        <p className={jobArticleClasses["job-data"]}>
          <b>Position: </b> {job.position}
        </p>
        <p className={jobArticleClasses["job-data"]}>
          <b>Started: </b>
          {job.startDate
            ? `${job.startDate.toLocaleString("default", {
                month: "long",
                year: "numeric",
              })}`
            : "-"}
        </p>
        <p className={jobArticleClasses["job-data"]}>
          <b>{job.endDate ? "Ended: " : "Ongoing"}</b>
          {job.endDate
            ? job.endDate.toLocaleString("default", {
                month: "long",
                year: "numeric",
              })
            : ""}
        </p>
        <p className={jobArticleClasses["job-description"]}>
          {job.description}
        </p>
        <h3>Responsibilities</h3>
        <ul className={jobArticleClasses["job-responsibilities-list"]}>
          {job.responsibilities.map((value) => (
            <li key={value} className={jobArticleClasses["responsibility"]}>
              {value}
            </li>
          ))}
        </ul>
        <p className={jobArticleClasses["job-next-prev-links"]}>
          {previousJobId && (
            <Link onClick={scrollToTop} href={`/expertise/${previousJobId}`}>
              {"\u2190"} Previous
            </Link>
          )}
          {nextJobId && (
            <Link onClick={scrollToTop} href={`/expertise/${nextJobId}`}>
              Next {"\u2192"}
            </Link>
          )}
        </p>
      </Section>
      <Footer />
    </>
  );
}
