import { h } from "preact";

import sectionClasses from "./Section.module.css";

export default function Section({
  className,
  children,
  ...props
}: Readonly<h.JSX.HTMLAttributes<HTMLElement>>) {
  const propsToSection = {
    className: `${sectionClasses["root"]} ${className?.toString() ?? ""}`,
    ...props,
  };

  return (
    <section {...propsToSection}>
      <article>{children}</article>
    </section>
  );
}
